.container-xxl .container .owl-carousel .project-item .position-relative a {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFFFFF;
    background: rgba(0, 0, 0, .5);
    border-radius: 6px;
    opacity: 0;
    transition: .5s;
}

.container-xxl .container .owl-carousel .project-item .position-relative:hover a {
    opacity: 1;
}

.container-xxl .container .project-carousel .owl-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 20px;
}

.container-xxl .container .project-carousel .owl-dot {
    width: 35px;
    height: 35px;
    margin: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #DEE2E6;
    border-radius: 35px;
    transition: .5s;
}


.container-xxl .container .project-carousel .owl-dot:hover,
.container-xxl .container .project-carousel .owl-dot.active {
    color: #FFFFFF;
    border-color: #009CFF;
    background: #009CFF;
}

.container-xxl .container .project-carousel {
    visibility: visible;
    animation-delay: 0.1s;
    animation-name: fadeInUp;
    margin: 3px; 
	padding: 3px; 
}

.container-xxl .container .project-carousel .project-item .position-relative img {
    max-width: 100%;
    height: auto;
}

.container-xxl .container .project-carousel .project-item {
    width: 310.25px;
    margin-right: 25px;
    display: inline-block;  
	margin-right: 3px;
}

.container-xxl .container .testimonial-carousel .testimonial-item .align-items-center img {
    width: 100px;
    height: 100px;
}

.container-xxl .container .testimonial-carousel .testimonial-item {
    width: 310.25px;
    margin-right: 25px;
    display: inline-block;  
	margin-right: 7.5px;
}

